import React, { useEffect } from "react"
import projects from "../db/projects"

const Instances = () => {
  useEffect(() => {
      if (typeof window !== 'undefined') {
          import('../animations/projectsAnimations')
              .then(({projectsAnimations}) => projectsAnimations())
      }
  }, [])

  return (
    <div className="projects-wrapper">
      <div id="p-container" className="projects-container">
        <div className="projects">
          {projects.map(p => {
            return (
              <a className="project" key={p.name} href={p.address}>
                <div className="name">{p.name}</div>
                <img
                  src={require(`../images/project-images/project-${p.id}.png`)}
                  alt={p.name}
                />
              </a>
            )
          })}
        </div>
      </div>
    </div>
  )
}

export default Instances
