import React, { useEffect } from "react"

const Picture = () => {
  useEffect(() => {
      if (typeof window !== 'undefined') {
          document.documentElement.className = "js"
          import('../animations/pictureAnimation')
              .then(({PictureAnimation}) => PictureAnimation())
      }

  }, [])

  return (
    <div className="pic-wrapper loading">
      <div className="pic-main">
        <div className="pic-item">
          <img
            id="my-picture"
            src={require("../images/mask.png")}
            alt=""
            className="js-image"
          />
        </div>
      </div>
      <div id="pic-container" />
    </div>
  )
}

export default Picture
